import React from 'react';
import { Row, Col } from 'reactstrap';

import styles from './animatedVsLive.module.scss';

import images from './animatedVsLive.images';

const title = 'ANIMATED VS. LIVE-ACTION';
const subTitle = 'Which promotional video meets your needs best.';
const date = '13/06/2020';

const data = [{
  desc: [{
    title: 'ANIMATED',
    desc: 'Animated videos work best for abstract concepts. They represent a way to simplify complex ideas by engaging with the audience.',
  }],
  img: images.animated,
  type: 'img-right',
}, {
  desc: [{
    title: 'Cons',
    desc: '<ul><li>High quality explainer video potentially takes longer to produce</li></ul>',
  }, {
    title: 'Pros',
    desc: `<ul>
<li>No sets to build / location to find</li>
<li>No actors to coddle</li>
<li>Budget friendly</li>
</ul>`,
  }],
  img: images.animated_2,
  type: 'img-left',
}, {
  desc: [{
    title: 'LIVE-ACTION',
    desc: 'Live-action videos carry authentic human touch to your videos. They communicate relatable emotions quickly that can give certain pieces of trustworthiness.',
  }],
  img: images.live_action,
  type: 'img-left',
}, {
  desc: [{
    title: 'Cons',
    desc: '<ul><li>• Demonstrate how the product/service fair in a real-life setting</li></ul>',
  }, {
    title: 'Pros',
    desc: `<ul>
<li>• High production cost</li>
<li>• Changes may require reshoots, which are time-consuming and at extra cos</li>
</ul>`,
  }],
  img: images.live_action_2,
  type: 'img-right',
}]

const animatedVsLive = () => {
  return (
    <div className={styles.container}>
      <h1>{title}</h1>
      <h2>{subTitle}</h2>
      <p className="date">{date}</p>
      {/* <div className="seperator" /> */}
      {
        data.map((d, i) => (
          <Row className={[styles.itemContainer, d.type].join(' ')} noGutters>
            <Col xs={{ order: 2, size: 12 }} sm={{ order: d.type === 'img-right' ? 1 : 2, size: 6 }}>
              {
                d.desc.map((desc, i2) => (
                  <div key={`desc-${i2}`}>
                    <h3 className={d.type === 'img-right' ? '' : 'text-right'} dangerouslySetInnerHTML={{ __html: desc.title }} />
                    <p className={d.type === 'img-right' ? '' : 'text-right'} dangerouslySetInnerHTML={{ __html: desc.desc }} />
                  </div>
                ))
              }
            </Col>
            <Col xs={{ order: 1, size: 12 }} sm={{ order: d.type === 'img-right' ? 2 : 1, size: 6 }}>
              <div className="image-container">
                <img {...d.img} alt={title} />
              </div>
            </Col>
          </Row>
        ))
      }
    </div>
  );
}

export default animatedVsLive;
